<script>
	document.title = "Very Simple Task Manager";

	import { fstore } from "./database.js";
	import { onMount } from "svelte";

	let bind, selectBind;

	onMount(async () => {
	  fstore
	    .collection("homework-1")
	    .get()
	    .then(ss => {
	      process(ss.docs);
	    });
	});

	let items = [];
	let tasks = [];
	let temp = [];

	function process(documents) {
	  for (let i = 0; i < documents.length; i++) {
	    let d = documents[i];
	    console.log(d.id, d.data());

	    let o = {
	      ...d.data(),
	      id: d.id
	    };

	    temp.push(o);
	  }
	  items = temp;
	  console.log(items);
	}

	function submit() {
	  event.preventDefault();
	  let formData = Object.fromEntries(new FormData(event.target));

	  fstore
	    .collection("homework-1")
	    .add(formData)
	    .then(ss => {
	      ss.get().then(doc => {
	        let temp = [...items];
	        console.log(doc.data());
	        console.log(doc.id);
	        temp.push(formData);
	        items = temp;
	      });
	    });

	  bind = "";
	  selectBind = "Incomplete";
	}

	function refresh() {
	  location.reload();
	}

	function del(value, index) {
	  fstore
	    .collection("homework-1")
	    .doc(value)
	    .delete();
	}

	function complete(value) {
	  fstore
	    .collection("homework-1")
	    .doc(value)
	    .update({
	      completed: true
	    });
	}

	function uncomplete(value) {
	  fstore
	    .collection("homework-1")
	    .doc(value)
	    .update({
	      completed: false
	    });
	}
</script>

<style>
	input,
	button,
	select {
	  font-family: "Times New Roman";
	  font-size: 14px;
	}

	hr {
	  visibility: hidden;
	  height: 0px;
	}

	main {
	  text-align: center;
	}
</style>

<main>
	<h1>Very Simple Task Manager</h1>
	<!-- Due dates and login/signup coming soon -->
	
	<ul>
		{#each items as v, i}
			<li 
				style={v.completed === true || v.completed === "true" ? "color:green" : "color:red"}>
				{v.task}
			</li><hr />

			<button on:click={() => del(v.id, i)}>Delete</button><span> </span>
			<button on:click={() => complete(v.id)}>Complete</button><span> </span>
			<button on:click={() => uncomplete(v.id)}>Uncomplete</button><hr /><hr />
		{/each}
	</ul>

	<form on:submit={() => submit()}>
		<input 
			type="text"
			name="task"
			placeholder="Do vocabulary worksheets"
			style="width:250px;"
			bind:value={bind}
		/>

		<select 
			style="height:21px;" 
			bind:value={selectBind}
			name="completed">

			<option value="false">Incomplete</option>
			<option value="true">Complete</option>
		</select>

		<button>Add</button>
	</form>
	<br />

	<span>Sync App: </span>
	<button on:click={() => refresh()}>ðŸ”„</button>
</main>